import { typeOf } from "mathjs";
import React from "react";
import "../Calcresult/Calc_result.css";

export default function Calc_results({ result }) {
  const _result = result.toString()

  return (

    <h1 className="result">
      { _result.length > 11 ? _result.slice(0,11):_result }
    </h1>
  );
}
