import React, { useState } from "react";
import Calctext from "./Pages/Calctext/Calc_text";
import Calcresults from "./Pages/Calcresult/Calc_results";
import Calcbutton from "./Components/Button/Calc_button";
import "./App.css";
import * as math from "mathjs";

export default function Projects() {
  const [text, setText] = useState("");
  const [result, setResult] = useState("");

  const addText = (val) => {
    setText((text) => [...text, val + ""]);
  };

  const clearText = () => {
    setText("");
    setResult("");
  };

  const calculateResult = () => {
    const Text = text.join("");

    setResult(math.evaluate(Text));
  };

  return (
    <div className="bg-dark">
      <div className="container project_container">
        <h1>Calculation Demo Page</h1>
        <div className="calc_container col-lg-4 col-md-12 col-10">
          <div className="calc_display_screen">
            <Calcresults result={result} />
            <Calctext text={text} />
          </div>
          <div className="calc_body">
            <div className="row">
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="7"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="8"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="9"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-success"
                  symbol="+"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="4"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="5"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol={"6"}
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-success"
                  symbol="-"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="1"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="2"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="3"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-success"
                  symbol="/"
                />
              </div>
            </div>
            <div className="row">
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="0"
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-secondary"
                  symbol="."
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={calculateResult}
                  classname="btn-secondary"
                  symbol="="
                />
              </div>
              <div className="col-3">
                <Calcbutton
                  handleText={addText}
                  classname="btn-success"
                  symbol="*"
                />
              </div>
            </div>
            <Calcbutton
              handleText={clearText}
              classname="btn-danger"
              symbol="Clear"
            />
          </div>
          <span className="calculator_name">LazkCalculator</span>
        </div>
      </div>
    </div>
  );
}
